<template>
  <div>
    <h1>欢迎使用航班推荐系统</h1>
    <p>1：人数最少为1，最多为8； 2：代理人最少为1人，最多为20人； 3：行程段数最多为8段，每段航班的出发日期在前一段之后</p>
    <p></p>
    <el-form :model="request" ref="request" label-width="60px" size="mini">
      <el-row>
        <el-col :offset=5 :span=3>
          <el-form-item
              prop="personNum"
              label="人数"
              :rules="{type:'number',required: true, message: '请输入人数(1~8)', min:1,max:8,trigger: 'blur'}"
          >
            <el-input v-model.number="request.personNum"></el-input>

          </el-form-item>
        </el-col>
        <el-col :offset=1 :span=4>
          <el-form-item
              label-width="100px"
              prop="totalCount"
              label="返回结果数"
              :rules="{type:'number',required: true, message: '请输入返回结果数', min:1,trigger: 'blur'}"
          >
            <el-input v-model.number="request.totalCount"></el-input>
          </el-form-item>
        </el-col>
        <el-col :offset=1 :span=5>
          <el-form-item label-width="90px"
                        prop="agencies"
                        label="代理人"
                        :rules="{required: true, message: '请选择代理人信息', trigger: 'blur'}"
          >
            <el-select v-model="request.agencies" multiple placeholder="请选择代理人信息">
              <el-option
                  v-for="item in agencyList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>

          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :offset=4 :span=5>
          <el-form-item label-width="90px"
                        v-for="(searchOptionFlightInfos, index) in request.searchOptionFlightInfos"
                        :label="'出发地'+(index+1)"
                        :key="searchOptionFlightInfos.key"
                        :prop="'searchOptionFlightInfos.'+(index)+'.departureCity'"
                        :rules="{required: true, message: '请输入出发城市', trigger: 'blur'}"
          >
            <el-input v-model="searchOptionFlightInfos.departureCity"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span=5>

          <el-form-item label-width="90px"
                        v-for="(searchOptionFlightInfos, index) in request.searchOptionFlightInfos"
                        :label="'目的地'+(index+1)"
                        :key="searchOptionFlightInfos.key"
                        :prop="'searchOptionFlightInfos.'+(index)+'.arrivalCity'"
                        :rules="{required: true, message: '请输入到达城市', trigger: 'blur'}"
          >
            <el-input v-model="searchOptionFlightInfos.arrivalCity"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span=9>
          <el-form-item label-width="100px" required
                        v-for="(searchOptionFlightInfos, index) in request.searchOptionFlightInfos"
                        :label="'出发日期'+(index+1)"
                        :key="searchOptionFlightInfos.key"
                        :rules="{required: true, message: '请输入出发日期', trigger: 'blur'}"
          >
            <el-col :span=12>
              <el-date-picker
                  v-model="searchOptionFlightInfos.departureTime"
                  type="date"
                  placeholder="选择日期"
                  :prop="'searchOptionFlightInfos.'+index+'.departureTime'"
                  :picker-options="pickerOptionsStart"
              >
              </el-date-picker>
            </el-col>
            <el-col :offset=1 :span=2>
              <el-button @click.prevent="removeDomain(searchOptionFlightInfos)">删除</el-button>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>

      <el-form-item>
        <el-button type="primary" @click="submitForm('request')">提交</el-button>
        <el-button @click="addsearchOptionFlightInfos">新增行程</el-button>
      </el-form-item>
    </el-form>

    <div v-if="resultOn">
      <el-row>
        <el-col :offset=2>
          <el-table width="150px"
                    :data="result"
                    style="width: 85%">
            <el-table-column label="行程结果">
              <template slot-scope="scope">
              {{scope.$index +1 + (currentPage-1) * 10}}
            </template>
            </el-table-column>
            <el-table-column v-for="index of result[0].flightDetailVos.length"
                             :label="'航段'+index+'（'+oldRequest.searchOptionFlightInfos[index-1].departureCity+'->'+oldRequest.searchOptionFlightInfos[index-1].arrivalCity+'）'"
                             :key="index.key"
                             align="center"
            >
              <el-table-column
                  :prop="'flightDetailVos.'+(index-1)+'.carrierName'"
                  label="承运人">
              </el-table-column>
              <el-table-column
                  :prop="'flightDetailVos.'+(index-1)+'.flightNo'"
                  label="航班编号">
              </el-table-column>
              <el-table-column label="起飞时间">
                <template slot-scope="scope">
                  {{ scope.row.flightDetailVos[index - 1].departureTime|dataFormat() }}
                </template>
              </el-table-column>
              <el-table-column label="到达时间">
                <template slot-scope="scope">
                  {{ scope.row.flightDetailVos[index - 1].arrivalTime | dataFormat() }}
                </template>
              </el-table-column>
              <el-table-column
                  :prop="'flightDetailVos.'+(index-1)+'.seatInfo'"
                  label="舱位信息">
                <template slot-scope="scope">
                  {{scope.row.flightDetailVos[index -1].seatInfo.length}}位旅客为{{ scope.row.flightDetailVos[index - 1].seatInfo.charAt(0)}}舱
                </template>
              </el-table-column>
            </el-table-column>
            <el-table-column
                prop="agency"
                label="代理人名称">
            </el-table-column>
            <el-table-column
                prop="totalPrice"
                label="总价格">
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <div style="margin-block: 20px" class="block">
        <el-button type="primary" icon="el-icon-arrow-left" @click="reducePage()">上一页</el-button>
        页码：{{this.currentPage}}
        <el-button type="primary" @click="addPage()">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      request: {
        personNum: '',
        agencies: '',
        totalCount: '',
        searchOptionFlightInfos: [{
          departureCity: '',
          arrivalCity: '',
          departureTime: ''
        }]
      },
      oldRequest:{},
      row: {},
      resultOn: false,
      currentPage: 1,
      result: [{
        agency: 'AAA001',
        totalPrice: 4396,
        flightDetailVos: [
          {
            carrierName: 'MH',
            flightNo: '0100',
            departureTime: '202305121221',
            arrivalTime: '202305121221',
            seatInfo: 'YYYY',
          },
        ]
      },
      ],
      resultDemo: [{
        agency: '',
        totalPrice: null,
        flightDetailVos: [
          {
            carrierName: '',
            flightNo: '',
            departureTime: null,
            arrivalTime: null,
            seatInfo: '',
          },
        ]
      }
      ],
      timeNew: Date.now(),
      pickerOptionsStart: {
        disabledDate: time => {
          return time.getTime() < this.timeNew - 8.64e7; /*今天及以后*/
        }
      },
      agencyList: [{
        value: 'AAA001',
        label: 'AAA001'
      }, {
        value: 'BBB001',
        label: 'BBB001'
      }, {
        value: 'CCC001',
        label: 'CCC001'
      }, {
        value: 'DDD001',
        label: 'DDD001'
      }, {
        value: 'EEE001',
        label: 'EEE001'
      }, {
        value: 'FFF001',
        label: 'FFF001'
      }, {
        value: 'GGG001',
        label: 'GGG001'
      }, {
        value: 'HHH001',
        label: 'HHH001'
      }, {
        value: 'III001',
        label: 'III001'
      }, {
        value: 'JJJ001',
        label: 'JJJ001'
      }, {
        value: 'KKK001',
        label: 'KKK001'
      }, {
        value: 'LLL001',
        label: 'LLL001'
      }, {
        value: 'MMM001',
        label: 'MMM001'
      }, {
        value: 'NNN001',
        label: 'NNN001'
      }, {
        value: 'OOO001',
        label: 'OOO001'
      }, {
        value: 'PPP001',
        label: 'PPP001'
      }, {
        value: 'QQQ001',
        label: 'QQQ001'
      }, {
        value: 'RRR001',
        label: 'RRR001'
      }, {
        value: 'SSS001',
        label: 'SSS001'
      }, {
        value: 'TTT001',
        label: 'TTT001'
      }, {
        value: 'UUU001',
        label: 'UUU001'
      }, {
        value: 'VVV001',
        label: 'VVV001'
      }, {
        value: 'WWW001',
        label: 'WWW001'
      }, {
        value: 'XXX001',
        label: 'XXX001'
      }, {
        value: 'YYY001',
        label: 'YYY001'
      }, {
        value: 'ZZZ001',
        label: 'ZZZ001'
      }],
    }
  },
  methods: {
    addPage(){
        this.$axios
            .post("http://localhost:80/searchTickets",{
              personNum:this.request.personNum,
              agencies:this.request.agencies,
              searchOptionFlightInfos:this.request.searchOptionFlightInfos,
              pageNum:this.currentPage+1
            })
            .then(
                res => {
                  this.currentPage += 1
                  if (res.data.pageNum === this.currentPage-1) {
                    this.$alert('当前页已是最后一页', '提示', {
                      confirmButtonText: '确定',
                    });
                    this.currentPage -= 1
                  } else {
                    this.result = res.data.data
                    this.resultOn = true
                    this.oldRequest = JSON.parse(JSON.stringify(this.request))
                    console.log(res)
                    console.log(this.result)
                  }
                }
            )
      },
    reducePage(){
      this.currentPage -= 1
      if (this.currentPage === 0){
        this.$alert('当前页已是第一页', '提示', {
          confirmButtonText: '确定',
        });
        this.currentPage = 1
      }else {
        this.$axios
            .post("http://localhost:80/searchTickets",{
              personNum:this.request.personNum,
              agencies:this.request.agencies,
              searchOptionFlightInfos:this.request.searchOptionFlightInfos,
              pageNum:this.currentPage
            })
            .then(
                res => {
                  if (res.data.data.length === 0) {
                    this.$alert('查询的结果为空,没有合适的航班', '提示', {
                      confirmButtonText: '确定',
                    });
                    this.result = this.resultDemo
                    this.resultOn = false
                    console.log(res)
                    console.log(this.result)
                  } else {
                    this.result = res.data.data
                    this.resultOn = true
                    this.oldRequest = JSON.parse(JSON.stringify(this.request))
                    console.log(res)
                    console.log(this.result)
                  }
                }
            )
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let temp = 0;
          if (this.request.searchOptionFlightInfos[0].departureTime === "" || this.request.searchOptionFlightInfos[0].departureTime == null) temp = 1
          for (let i = 0; i < this.request.searchOptionFlightInfos.length - 1; i++) {
            if (this.request.searchOptionFlightInfos[i+1].departureTime === "" || this.request.searchOptionFlightInfos[i+1].departureTime == null ) temp = 1
            if (this.request.searchOptionFlightInfos[i].departureTime > this.request.searchOptionFlightInfos[i + 1].departureTime) {
              temp = 1;
            }
          }
          if (temp === 1) {
            this.$alert('输入的时间不符合规定，请重新输入', '提示', {
              confirmButtonText: '确定',
            });
          } else {
            this.$message('提交成功');
            console.log(this.$data)
            this.$axios
                .post("http://localhost:80/searchTickets",{
                  personNum:this.request.personNum,
                  agencies:this.request.agencies,
                  searchOptionFlightInfos:this.request.searchOptionFlightInfos,
                  pageNum:1
                })
                .then(
                    res => {
                      this.currentPage = 1
                      if (res.data.data.length === 0) {
                        this.$alert('查询的结果为空,没有合适的航班', '提示', {
                          confirmButtonText: '确定',
                        });
                        this.result = this.resultDemo
                        this.resultOn = false
                        console.log(res)
                        console.log(this.result)
                      } else {
                        this.result = res.data.data
                        this.resultOn = true
                        this.oldRequest = JSON.parse(JSON.stringify(this.request))
                        console.log(res)
                        console.log(this.result)
                      }
                    }
                ).catch(function (error) {
              console.log(error)
            })
          }
        } else {
          this.$message('提交失败');
          return false;
        }
      });
    },
    removeDomain(item) {
      const index = this.request.searchOptionFlightInfos.indexOf(item);
      if (this.request.searchOptionFlightInfos.length !== 1) {
        this.request.searchOptionFlightInfos.splice(index, 1)
      } else {
        this.$message('最少一条行程');
      }
    },
    addsearchOptionFlightInfos() {
      if (this.request.searchOptionFlightInfos.length === 8) {
        this.$message('最多只能添加八段行程');
      } else {
        this.request.searchOptionFlightInfos.push({
          value: '',
          key: Date.now()
        })
      }
    },
  },
  filters: {
    dataFormat: function (str) {
      return `${str.substr(0, 4)}年${str.substr(4, 2)}月${str.substr(6, 2)}日${str.substr(8, 2)}时${str.substr(10, 2)}分`
    },
  }
}
</script>
